.backdrob {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: var(--orange-2);
  z-index: 100;
}
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--white);
  border-radius: 0.5rem;
  z-index: 200;
}

.modal__body {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 5rem;
}
.modal__title {
  font-weight: 700;
  font-size: 1.5rem;
  text-align: center;
}
.modal__data {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.modal__label {
  font-size: 1.2rem;
  color: var(--silver);
}
.modal__input {
  border: 1px solid var(--primery);
  box-shadow: 0.25rem 0.5rem 1rem rgba(112, 192, 91, 0.2);
  border-radius: 0.25rem;
  font-size: 1.5rem;
  color: var(--black);
  padding: 0.8rem 1.2rem;
}

.modal__input:focus {
  outline-color: var(--primery);
}
.modal__button {
  font-size: 1.5rem;
  background-color: var(--white);
  border-radius: 0.25rem;
  color: var(--secondary);
  margin-top: 2.3rem;
  padding: 1rem;
  border-color: var(--secondary);
}

.modal__button:hover {
  background-color: var(--secondary);
  color: var(--secondary-disabled);
}

.modal__registration {
  font-size: 0.8rem;
  border: 1px solid var(--primery);
  border-radius: 4px;
  color: var(--primery);
  padding: 0.5rem;
  background-color: var(--white);
}

.modal__registralion:hover {
  background-color: var(--primery);
  color: var(--white);
}
.modal__close {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: 0%;
  left: 100%;
  transform: translate(-100%, 0);
  cursor: pointer;
  background-color: var(--cream-2);
}

@media screen and (max-width: 500px) {
  .modal__body {
    gap: 1rem;
    padding: 2rem;
  }
  .modal__input {
    font-size: 1rem;
    color: var(--black);
    padding: 0.4rem 0.6rem;
  }
  .modal__button {
    font-size: 1rem;
    margin-top: 1.1rem;
    padding: 0.5rem;
  }
  .modal__registration {
    font-size: 0.8rem;
    padding: 0.25rem;
  }
  .modal__close {
    display: flex;
    width: 2rem;
    height: 2rem;
  }
}
